@import url('https://fonts.googleapis.com/css2?family=Do+Hyeon&family=Kirang+Haerang&family=Plaster&display=swap');

html {
  font-size: 18px;
}

body {
  --black: #000000;
  --ash-black: #222;
  --white: #fafafa;
  --sky: #00ccff;
  --green: #22dddd;
  --blue: #1300ff;
  --dusk: #6600ff;
  --purple: #9900ff;
  --pink: #ff0066;
  --red: #fe0222;
  --orange: #fd7702;
  --yellow: #ffbb00;
  --grape: #fec594;

  --background: var(--grape);
  --accent: var(--white);

  margin: 0;
  padding: 0;
  background-color: var(--background);
  color: var(--accent);
  overflow: hidden;
  touch-action: none;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

* {
  font-family: 'Kirang Haerang', sans-serif;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}

h1,
h2,
h3 {
  font-weight: 600;
}

h1 {
  font-weight: 600;
  letter-spacing: -1px;
  line-height: 1.2;
  text-align: center;
  margin: 100px 0 40px;
}

h2 {
  font-weight: 400;
  margin: 50px 0 10px;
}

p {
  margin: 0 0 30px 0;
}

::-webkit-scrollbar {
  height: 5px;
  width: 5px;
  background: var(--background);
}

::-webkit-scrollbar-thumb {
  background: var(--accent);
  -webkit-border-radius: 1ex;
}

::-webkit-scrollbar-corner {
  background: var(--background);
}

img {
  /* position: absolute;
 right: 0;
 bottom: 0; */
  width: 100%;
  height: 100%;
}

#progress {
  position: fixed;
  top: 20px;
  left: 20px;
  transform: rotate(-90deg);
}

circle {
  stroke-dashoffset: 0;
  stroke-width: 15%;
  fill: none;
}

.bg {
  stroke: var(--accent);
  opacity: 0.3;
}

#progress .indicator {
  stroke: var(--accent);
}

.react-horizontal-scrolling-menu--arrow-right,
.react-horizontal-scrolling-menu--arrow-left {
  position: absolute;
  z-index: 999;
  top: 50%;
  transform: translateY(-505);
  -webkit-transform: translateY(-505);
  -moz-transform: translateY(-505);
  -ms-transform: translateY(-505);
  -o-transform: translateY(-505);
}

.react-horizontal-scrolling-menu--arrow-right {
  right: 0;
}

.react-horizontal-scrolling-menu--arrow-left {
  left: 0;
}

@media screen and (max-width: 760px) {
  .react-horizontal-scrolling-menu--scroll-container {
    gap: 90px !important;
  }
}